@font-face {
    font-family: 'Paro';
    src: url('../static/fonts/Paro-Regular.eot');
    src: url('../static/fonts/Paro-Regular.eot?#iefix') format('embedded-opentype'), url('../static/fonts/Paro-Regular.woff2') format('woff2'), url('../static/fonts/Paro-Regular.woff') format('woff'), url('../static/fonts/Paro-Regular.ttf') format('truetype'), url('../static/fonts/Paro-Regular.svg#Paro-Regular') format('svg');
    font-weight: normal;
    font-style: normal;
    font-display: swap;
}

@font-face {
    font-family: 'Urbanist';
    src: url('../static/fonts/Urbanist-Regular.eot');
    src: url('../static/fonts/Urbanist-Regular.eot?#iefix') format('embedded-opentype'),
        url('../static/fonts/Urbanist-Regular.woff2') format('woff2'),
        url('../static/fonts/Urbanist-Regular.woff') format('woff'),
        url('../static/fonts/Urbanist-Regular.ttf') format('truetype'),
        url('../static/fonts/Urbanist-Regular.svg#Urbanist-Regular') format('svg');
    font-weight: normal;
    font-style: normal;
    font-display: swap;
}

$marron: #9B7764;
$bleuC: #64889B;
$bleuF: #092327;


*
{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

html{
    overscroll-behavior: none;
    overflow: hidden;
}

::-webkit-scrollbar {
    display: none;
}

.webgl
{
    position: fixed;
    top: 0;
    left: 50vw;
    outline: none;
    z-index: -1;
}

section{
    width: 65vw;
    height: 100vh;
    background-color: white;
    // font-size: 5em;
    position: fixed;
    // transition: transform 1s cubic-bezier(0.65, 0, 0.35, 1);
    transform: translateX(-100%);
    @media only screen and (max-width: 1100px)  {
        width:100vw;
    }
}

.teaser{
    width: 50vw;
    height: 100vh;
    transform: translateX(0%);
    padding: 4em 4.5em;
    display: flex;
    flex-direction: column;
    &__title{
        font-family: 'Paro';
        font-size: 8em;
        color: $bleuC;
        margin-bottom: 1em;
    }
    &__text{
        font-family: 'Urbanist';
        font-size: 1.2em;
        color: $bleuF;
        margin-bottom: 5em;
        width: 70%;
    }
    &__button{
        font-family: 'Urbanist';
        font-size: 1.2em;
        color: $marron;
        border: 1px solid $marron;
        margin-bottom: 1em;
        width: fit-content;
        padding: 0.5em 0.8em;
        cursor: pointer;
    }
    &__socials{
        margin-top: auto;
        font-family: 'Urbanist';
        font-size: 1em;
        width: 12em;
        &--links{
            color: $bleuF;
            width: 1.75em;
            &:nth-child(2){
                width: 1.65em;
            }
            &:nth-child(3){
                width: 2.1em;
            }
        }
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
}

