@media screen and (max-width: 950px) {
    html {
        overscroll-behavior: none;
        overflow: auto;
    }
    body{
        display: flex;
        flex-direction: column;
    }
    .webgl {
        position: static;
        left: 0;
    }
    .teaser {
        position: static;
        // margin-top: 33vh;
        width: auto;
        height: auto;
        transform: translateX(0%);
        padding: 2.2em 2em;
        &__title {
            font-size: 5em;
            margin-bottom: 1em;
        }
        &__text {
            font-size: 1.1em;
            margin-bottom: 5em;
            width: 80%;
        }
        &__button {
            font-size: 1.1em;
            margin-bottom: 5em;
            padding: 0.5em 0.8em;
        }
        &__socials {
            margin-top: auto;
            font-size: 1em;
        }
    }
}
